.productPageContainer {
  display: flex;
  flex-direction: row;
  padding: 20px; /*    */
  gap: 30px;
  max-width: 1200px;
  margin: 20px auto;
  font-family: Arial, sans-serif;
  align-items: flex-start;
  box-sizing: border-box; /*  []      */
}

.leftPanel {
  flex-grow: 5.5;
  flex-shrink: 1;
  flex-basis: 0;
  display: flex;
  flex-direction: column; /*      */
  justify-content: flex-start; /*     */
  align-items: stretch; /*      ()  center   */
  min-width: 0;
  
  /* transform  position         */
  /* transform: scale(1.2); */
  /* position: relative; */
  /* top: 100px; */

  /* height  max-height  */
  /*  A:    ,   (, rightPanel   ) */
  /*   height, max-height   , overflow-y: auto  .
       productPageContainer align-items: flex-start     . */

  /*  B:   ,   (leftPanel rightPanel    ) */
  max-height: calc(100vh - 40px - 70px); /*      */
  overflow-y: auto;
  
  padding-right: 8px; /*   */
}

.rightPanel {
  flex-grow: 4.5; /*    (45% ,  10 ) */
  flex-shrink: 1; /*       */
  flex-basis: 0;  /*    0 */
  display: fixed;
  flex-direction: column;
  min-width: 0; /*  :       */
  position: relative;
  left: 160px;
  top: 50px;
  padding-bottom: 65px; /* ActionBar  */

  /*      () */
  height: calc(100vh - 40px - 70px); /*  [] leftPanel    */
  /*   : height: 600px; */
  overflow-y: auto; /*  []      */
  padding-right: 8px; /*    () */
  /* border: 1px dashed blue; */
}

.loadingMessage,
.errorMessage,
.infoMessage {
  width: 100%;
  text-align: center;
  padding: 50px 20px;
  font-size: 1.2em;
  color: #555;
}

.errorMessage {
  color: red;
}

@media (max-width: 992px) {
  .productPageContainer {
    flex-direction: column;
    gap: 20px;
    align-items: stretch; /*  []      */
  }
  .leftPanel,
  .rightPanel {
    flex: none;
    width: 100%;
  }
}

/* ProductPage.module.css   */
.productReviewsSection {
  margin-top: 40px;
  padding-top: 20px;
  border-top: 1px solid #eee;
  width: 60%;
  position: relative;
  top: -215px;
  left: 43px;
}

.productReviewsSection h2 {
  font-size: 1.4em;
  margin-bottom: 20px;
}

.reviewList {
  list-style: none;
  padding: 0;
}

.reviewItem {
  border-bottom: 1px solid #f0f0f0;
  padding: 20px 0;
}
.reviewItem:last-child {
  border-bottom: none;
}

.reviewAuthorInfo {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 8px;
}

.reviewAuthorName {
  font-weight: bold;
  font-size: 0.95em;
}

.reviewDate {
  font-size: 0.85em;
  color: #777;
}

.reviewRating {
  margin-bottom: 10px;
  color: #f8c102; /*   */
  font-size: 1.1em;
}

.reviewImage {
  max-width: 150px; /*     */
  max-height: 150px;
  object-fit: cover;
  border-radius: 4px;
  margin-bottom: 12px;
  display: block; /*  block   */
}

.reviewComment {
  font-size: 0.9em;
  line-height: 1.6;
  color: #333;
  white-space: pre-wrap; /*     */
}