/* PromotionBanner.module.css */
.bannerContainer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #e6f7ff; /*     */
    padding: 12px 16px;
    margin: 16px 0; /*   */
    font-size: 14px;
    color: #1890ff; /*   */
    cursor: pointer;
    border-radius: 4px; /*    */
  }
  
  .bannerContainer:hover {
    background-color: #d9f0fa;
  }
  
  .bannerContainer .arrowIcon {
    color: #1890ff;
    font-size: 16px;
  }