.actionBarContainer {
  /* position: fixed; */ /*  []    */
  /* bottom: 0; */       /*  [] */
  /* left: 1037px; */    /*  [] */
  /* right: 0; */       /*  [] */
  /* max-width: 480px; */ /*  [  rightPanel   100% ] */
  width: 100%; /* (rightPanel)   */
  /* margin: 0 auto; */   /*  [  ] */
  margin-top: auto; /*  [] rightPanel      (   ) */

  display: flex;
  align-items: center;
  background-color: #fff;
  border-top: 1px solid #ddd;
  padding: 10px 16px;
  box-shadow: 0 -2px 5px rgba(0, 0, 0, 0.05); /*   */
  height: 65px;
  box-sizing: border-box;
  position: relative;
  top: 50px;
}
  
  .wishlistButton {
    display: flex;
    flex-direction: column; /*     */
    align-items: center;
    justify-content: center;
    background: none;
    border: none;
    cursor: pointer;
    padding: 0;
    margin-right: 16px; /*     */
    color: #555;
    min-width: 40px; /*   */
  }
  
  .wishlistIcon {
    color: #777; /*      */
    font-size: 22px; /*   */
    /*     color: #e83e8c; */
  }
  
  .wishlistCount {
    font-size: 11px;
    margin-top: 2px;
    color: #555;
  }
  
  .actionButtonsGroup {
    flex-grow: 1; /*     */
    display: flex;
    gap: 10px; /*    */
  }
  
  .cartButton,
  .buyButton {
    flex-grow: 1; /*     */
    padding: 12px 0; /*   */
    border-radius: 4px;
    font-size: 15px;
    font-weight: bold;
    cursor: pointer;
    text-align: center;
    border: 1px solid; /*    */
  }
  
  .cartButton {
    background-color: #fff;
    border-color: #ccc;
    color: #333;
  }
  
  .cartButton:hover {
    background-color: #f8f8f8;
  }
  
  .buyButton {
    background-color: #000; /*   */
    border-color: #000;
    color: #fff; /*   */
  }
  
  .buyButton:hover {
    background-color: #333; /*     */
  }