/* src/components/SearchModal/SearchModal.module.css */
.modalOverlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5); /*   */
    display: flex;
    justify-content: center; /*    */
    align-items: center; /*    */
    z-index: 1000;
  }
  
  .modalContainer {
    background-color: #fff;
    border-radius: 8px; /*   */
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
    width: 90%;
    max-width: 500px; /*    */
    /* height: 80vh;      */
    max-height: 85vh; /*   ,   */
    display: flex;
    flex-direction: column;
    overflow: hidden; /*      + border-radius  */
  }
  
  .modalContent {
    padding: 15px 20px;
    overflow-y: auto; /*      */
    flex-grow: 1; /*    */
  }
  
  /*   ( ) */
  .modalContent::-webkit-scrollbar {
    width: 8px;
  }
  
  .modalContent::-webkit-scrollbar-thumb {
    background-color: #ccc;
    border-radius: 4px;
  }
  
  .modalContent::-webkit-scrollbar-track {
    background-color: #f0f0f0;
  }
  
