/* src/styles/ProductRegistrationForm.module.css */
.container {
    padding: 20px;
    max-width: 800px;
    margin: 90px auto 0; /* Header   */
    background-color: #fff;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.pageTitle {
    text-align: center;
    color: #333;
    margin-bottom: 30px;
    font-size: 2em;
    font-weight: 600;
}

.form {
    display: flex;
    flex-direction: column;
    gap: 20px; /*  formGroup   */
}

.formGroup {
    display: flex;
    flex-direction: column;
}

.label {
    margin-bottom: 8px;
    font-weight: 600;
    color: #555;
    font-size: 0.95em;
}

.required {
    color: red;
    margin-left: 4px;
}

.input,
.textarea,
.select {
    padding: 12px;
    border: 1px solid #ddd;
    border-radius: 6px;
    font-size: 1em;
    transition: border-color 0.2s ease-in-out;
}

.input:focus,
.textarea:focus,
.select:focus {
    border-color: #007bff;
    outline: none;
    box-shadow: 0 0 0 2px rgba(0, 123, 255, 0.25);
}

.textarea {
    resize: vertical; /*     */
    min-height: 100px;
}

.fileInput {
    padding: 8px; /*       */
}

.uploadButton {
    background-color: #28a745;
    color: white;
    border: none;
    padding: 10px 15px;
    border-radius: 5px;
    cursor: pointer;
    font-size: 0.9em;
    margin-top: 10px;
    transition: background-color 0.2s ease;
    align-self: flex-start; /*    */
}

.uploadButton:hover {
    background-color: #218838;
}

.uploadButton:disabled {
    background-color: #ccc;
    cursor: not-allowed;
}

.imagePreviewContainer {
    margin-top: 20px;
    border-top: 1px solid #eee;
    padding-top: 20px;
}

.subTitle {
    font-size: 1.1em;
    color: #444;
    margin-bottom: 10px;
}

.imagePreviewList {
    list-style: none;
    padding: 0;
    display: flex;
    flex-wrap: wrap; /*     */
    gap: 15px;
}

.imagePreviewItem {
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 1px solid #eee;
    padding: 10px;
    border-radius: 4px;
}

.previewImage {
    max-width: 150px; /*    */
    max-height: 150px;
    object-fit: cover; /*     */
    border-radius: 4px;
    margin-bottom: 8px;
}

.imageUrlText {
    font-size: 0.8em;
    color: #777;
    word-break: break-all; /*  URL  */
    text-align: center;
}

.submitButton {
    background-color: #007bff;
    color: white;
    border: none;
    padding: 15px;
    border-radius: 6px;
    cursor: pointer;
    font-size: 1.1em;
    font-weight: 600;
    transition: background-color 0.2s ease;
    margin-top: 10px; /*    */
}

.submitButton:hover {
    background-color: #0056b3;
}

.submitButton:disabled {
    background-color: #aaa;
    cursor: not-allowed;
}