.myReviewsContainer {
    max-width: 900px; /*    */
    margin: 0 auto;
    padding: 0 20px 20px 20px; /*   ,   */
    display: flex; /*    */
    flex-wrap: wrap; /*   */
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    position: relative;
    top: 80px;
  }
  
  .header {
    width: 100%;
    display: flex;
    align-items: center;
    padding: 15px 0; /*    */
    border-bottom: 1px solid #eee;
    margin-bottom: 20px;
  }
  
  .header h1 {
    font-size: 18px;
    font-weight: 600;
    text-align: center;
    flex-grow: 1;
    margin: 0;
  }
  
  .backButton {
    background: none;
    border: none;
    font-size: 20px;
    cursor: pointer;
    color: #333;
    padding-right: 10px; /*   */
  }
  
  .tabContainer {
    width: 100%; /*    */
    display: flex;
    margin-bottom: 20px;
    border-bottom: 1px solid #eee;
  }
  
  .tabButton {
    flex: 1;
    padding: 12px 0;
    font-size: 15px;
    font-weight: 500;
    color: #888;
    background-color: transparent;
    border: none;
    border-bottom: 2px solid transparent;
    cursor: pointer;
    transition: color 0.2s, border-bottom-color 0.2s;
  }
  
  .tabButton.activeTab {
    color: #333;
    font-weight: bold;
    border-bottom-color: #333;
  }
  
  .pointBanner {
    width: 100%;
    background-color: #fffbeb; /*    */
    border: 1px solid #ffe58f; /*   */
    color: #fa8c16; /*   */
    padding: 12px 15px;
    border-radius: 6px;
    font-size: 13px;
    margin-bottom: 20px;
    text-align: center;
  }
  
  .pointHighlight {
    font-weight: bold;
  }
  
  .contentArea {
    flex-grow: 1; /*       */
    width: calc(100% - 280px); /*    (  ) */
    margin-right: 20px; /*   */
  }
  @media (max-width: 768px) {
      .contentArea {
          width: 100%;
          margin-right: 0;
          margin-bottom: 20px;
      }
  }
  
  
  .loadingText,
  .emptyText,
  .errorText {
    text-align: center;
    padding: 40px 0;
    color: #888;
    font-size: 15px;
  }
  .errorText {
      color: red;
  }
  
  /*   */
  .sideInfo {
    width: 260px; /*   */
    /* border-left: 1px solid #f0f0f0; */ /*  () */
    /* padding-left: 20px; */ /*   () */
  }
  @media (max-width: 768px) {
      .sideInfo {
          width: 100%;
          border-left: none;
          padding-left: 0;
      }
  }
  
  
  .qrSection, .contactSection {
      background-color: #f9f9f9;
      padding: 20px;
      border-radius: 8px;
      text-align: center;
      margin-bottom: 20px;
  }
  .appIcon {
      width: 40px;
      height: 40px;
      margin-bottom: 10px;
  }
  .qrSection p {
      font-size: 13px;
      color: #555;
      margin: 5px 0 10px 0;
      line-height: 1.5;
  }
  .qrSection p strong {
      color: #333;
  }
  .qrCode {
      width: 100px;
      height: 100px;
      margin: 10px auto;
      display: block;
  }
  .qrText {
      font-size: 12px !important;
      color: #888 !important;
  }
  
  .contactTitle {
      font-size: 14px;
      font-weight: bold;
      color: #333;
      margin-bottom: 15px;
  }
  .contactEmail {
      font-size: 13px;
      color: #555;
      margin-bottom: 15px;
  }
  .copyButton {
      font-size: 13px;
      padding: 8px 15px;
      border: 1px solid #ddd;
      background-color: #fff;
      border-radius: 4px;
      cursor: pointer;
  }
  .copyButton:hover {
      background-color: #f5f5f5;
  }

  .myReviewsContainer {
    max-width: 900px; 
    margin: 0 auto;
    /* padding: 0 20px 20px 20px; */ /*  mainContentLayout     */
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    /* position: relative; */ /*    , Header   */
    /* top: 80px; */       /*     */
  }
  
  /* , ,       */
  .header, .tabContainer, .pointBanner {
      width: 100%;
      padding-left: 20px; /*     */
      padding-right: 20px;
      box-sizing: border-box; /*    */
  }
  
  
  /*        */
  .mainContentLayout {
    display: flex;
    flex-direction: row; /*   () */
    justify-content: space-between; /*   () */
    padding: 0 20px; /*   */
    box-sizing: border-box;
    width: 100%;
    margin-top: 20px; /* /  */
  }
  
  .contentArea {
    flex-grow: 1; /*     */
    /* width: calc(100% - 280px); */ /* flex-grow   width     */
    /* margin-right: 20px; */ /* justify-content: space-between      */
    /*       */
    /* flex-basis: 70%; */
  }
  
  .sideInfo {
    width: 260px;
    flex-shrink: 0; /*   ,   */
    margin-left: 20px; /*    */
  }
  
  /*  :        */
  @media (max-width: 768px) {
    .mainContentLayout {
      flex-direction: column; /*   */
    }
    .contentArea {
      width: 100%;
      margin-right: 0;
      margin-bottom: 20px; /*   */
    }
    .sideInfo {
      width: 100%;
      margin-left: 0;
    }
  }