/* ReviewWriteForm.module.css */
.reviewWriteContainer {
    max-width: 700px; /*     */
    margin: 0 auto;
    padding: 20px;
    font-family: Arial, sans-serif;
    background-color: #fff;
  }
  
  .header {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
    padding-bottom: 15px;
    border-bottom: 1px solid #eee;
  }
  
  .header h1 {
    font-size: 20px;
    font-weight: bold;
    text-align: center;
    flex-grow: 1; /*    */
    margin: 0;
  }
  
  .backButton {
    background: none;
    border: none;
    font-size: 24px;
    cursor: pointer;
    padding: 0 10px 0 0;
    color: #333;
  }
  
  .productInfoSection {
    display: flex;
    align-items: center;
    margin-bottom: 30px;
    padding: 15px;
    background-color: #f9f9f9;
    border-radius: 8px;
  }
  
  .productImage {
    width: 60px;
    height: 60px;
    object-fit: cover;
    border-radius: 4px;
    margin-right: 15px;
    border: 1px solid #eee;
  }
  
  .productNameAndOption {
    display: flex;
    flex-direction: column;
  }
  
  .productName {
    font-size: 15px;
    font-weight: bold;
    color: #333;
    margin: 0 0 4px 0;
  }
  
  /*    
  .productOption {
    font-size: 13px;
    color: #777;
    margin: 0;
  }
  */
  
  .reviewForm {
    display: flex;
    flex-direction: column;
    gap: 25px; /*     */
  }
  
  .sectionTitle {
    font-size: 16px;
    font-weight: bold;
    margin-bottom: 10px;
    color: #333;
  }
  
  /*  */
  .ratingSection .stars {
    display: flex;
  }
  
  .ratingSection .star {
    font-size: 30px;
    color: #ddd; /*    */
    cursor: pointer;
    margin-right: 5px;
    transition: color 0.2s;
  }
  
  .ratingSection .star:hover,
  .ratingSection .star.selected {
    color: #fadb14; /* /    */
  }
  
  /*   */
  .commentSection {
    /*  */
  }
  
  .commentTextarea {
    width: 100%;
    padding: 12px;
    border: 1px solid #ddd;
    border-radius: 4px;
    font-size: 14px;
    box-sizing: border-box;
    resize: vertical;
    min-height: 120px;
  }
  
  .commentLength {
    text-align: right;
    font-size: 12px;
    color: #888;
    margin-top: 5px;
  }
  
  /*   */
  .photoAttachSection {
    /*  */
  }
  
  .photoAttachButton {
    display: inline-flex; /*    */
    align-items: center;
    padding: 10px 15px;
    border: 1px solid #ccc;
    border-radius: 4px;
    cursor: pointer;
    background-color: #f9f9f9;
    font-size: 14px;
    color: #555;
  }
  .photoAttachButton:hover {
    background-color: #f0f0f0;
  }
  
  .cameraIcon {
    margin-right: 8px;
    font-size: 18px;
  }
  
  .photoInput {
    display: none; /*  input  */
  }
  
  .previewContainer {
    margin-top: 15px;
    position: relative;
    display: inline-block; /*    */
  }
  
  .previewImage {
    max-width: 150px;
    max-height: 150px;
    border-radius: 4px;
    border: 1px solid #eee;
    object-fit: cover;
  }
  .removeImageButton {
      position: absolute;
      top: -5px;
      right: -5px;
      background-color: rgba(0,0,0,0.6);
      color: white;
      border: none;
      border-radius: 50%;
      width: 20px;
      height: 20px;
      font-size: 12px;
      line-height: 20px;
      text-align: center;
      cursor: pointer;
      padding: 0;
  }
  
  .photoAttachGuide {
    font-size: 12px;
    color: #888;
    margin-top: 10px;
  }
  
  /*  */
  .errorMessage {
    color: red;
    font-size: 14px;
    margin-top: 10px;
    text-align: center;
  }
  .successMessage {
      color: green;
      font-size: 14px;
      margin-top: 10px;
      text-align: center;
  }
  
  
  /*   */
  .submitButton {
    padding: 12px 20px;
    background-color: #5cb85c; /*   */
    color: white;
    border: none;
    border-radius: 4px;
    font-size: 16px;
    font-weight: bold;
    cursor: pointer;
    transition: background-color 0.2s;
    width: 100%;
    margin-top: 20px;
  }
  
  .submitButton:hover {
    background-color: #4cae4c;
  }
  
  .submitButton:disabled {
    background-color: #ccc;
    cursor: not-allowed;
  }