/* Signup.css */

.box {
  /*      ,  1150px (   ) */
  height: 1200px; /*  1026px  */
  width: 1919px;
}

/* style.css  fixed   */
.box .view {
  position: relative; /*  fixed  */
  width: auto;        /* or  width */
  height: auto;       /* form    auto */
}

.box .overlap {
  background-color: #ffffff;
  /*       */
  height: 1200px; /*  1026px  */
  position: relative;
  width: 1919px;
}

.box .rectangle {
  background-color: #ffffff80;
  /*       */
  height: 1200px; /*  1026px  */
  left: 0;
  position: absolute;
  top: 0;
  width: 1919px;
}

/* --- Navigation Styles ( ) --- */
.box .nav {
  align-items: flex-start;
  display: flex;
  flex-direction: column;
  height: 70px;
  justify-content: center;
  left: 51px;
  position: absolute;
  top: 0;
  width: 1799px;
}

.box .navbar {
  align-self: stretch;
  background-color: #1a1b1f;
  flex: 1;
  flex-grow: 1;
  position: relative;
  width: 100%;
}

