.pointsInfoContainer {
    background-color: #fff;
    padding: 16px;
    border-top: 1px solid #eee;
    border-bottom: 1px solid #eee;
    margin-bottom: 16px;
  }
  
  .maxPointsSection {
    font-size: 14px;
    color: #444;
    margin-bottom: 12px; /*   */
    /* padding-bottom: 12px;     padding-bottom  div.separator  */
    position: relative; /* separator   */
  }
  
  .maxPointsValue {
    font-weight: bold;
    color: #e53935; /*    */
  }
  
  .separator {
    height: 1px;
    background-color: #f0f0f0;
    margin-top: 12px; /*   */
    margin-bottom: 12px; /* detailsSection  */
  }
  
  .detailsSection {
    background-color: #f9f9f9; /*    */
    padding: 12px;
    border-radius: 4px;
  }
  
  .pointItem {
    display: flex;
    justify-content: space-between;
    font-size: 13px;
    color: #666;
    margin-bottom: 8px;
  }
  
  .pointItem:last-child {
    margin-bottom: 0;
  }
  
  .pointItem .pointValue {
    font-weight: 500; /*   */
    color: #333;
  }