.optionsContainer {
    background-color: #f7f7f7; /*     */
    padding: 16px;
    margin-bottom: 0; /* TotalSummary    */
    border-radius: 6px;
  }
  
  .optionName {
    font-size: 15px;
    font-weight: 500;
    color: #333;
    margin-bottom: 12px;
  }
  
  .controlsAndPrice {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .quantityControl {
    display: flex;
    align-items: center;
    border: 1px solid #ccc;
    border-radius: 4px;
    overflow: hidden; /*    */
  }
  
  .quantityButton {
    background-color: #fff;
    border: none;
    color: #555;
    font-size: 18px; /*    */
    font-weight: normal; /*  -, + bold  */
    cursor: pointer;
    width: 32px;
    height: 32px;
    display: flex;
    justify-content: center;
    align-items: center;
    line-height: 1; /*    */
  }
  
  .quantityButton:disabled {
    color: #bbb;
    cursor: not-allowed;
    background-color: #f0f0f0;
  }
  
  .decreaseButton {
    border-right: 1px solid #ccc;
  }
  
  .increaseButton {
    border-left: 1px solid #ccc;
  }
  
  .quantityDisplay {
    width: 40px;
    text-align: center;
    font-size: 14px;
    font-weight: 500;
    color: #333;
    padding: 0 5px; /*   */
    background-color: #fff; /*   */
    height: 32px;
    line-height: 32px; /*    */
  }
  
  .currentOptionPrice {
    font-size: 18px;
    font-weight: bold;
    color: #333;
  }