/* src/components/ProductCard/ProductCard.module.css */
.card {
    background-color: #fff;
    border: 1px solid transparent; /*        */
    font-family: Arial, sans-serif;
    display: flex;
    flex-direction: column;
  }
  
  .card:hover {
    /* box-shadow: 0 4px 8px rgba(0,0,0,0.1); */
    /* border-color: #ddd; */
  }
  
  .imageLink {
    text-decoration: none;
    color: inherit;
  }
  
  .imageContainer {
    position: relative;
    width: 100%;
    /* padding-top: 125%; 4:5  */
    aspect-ratio: 4 / 5; /*    */
    overflow: hidden;
    background-color: #f7f7f7; /*     */
  }
  
  .productImage {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover; /*     */
    transition: transform 0.3s ease;
  }
  
  .card:hover .productImage {
    transform: scale(1.05); /*      */
  }
  
  .tag {
    position: absolute;
    bottom: 8px;
    left: 8px;
    background-color: rgba(255, 82, 82, 0.9); /*   */
    color: white;
    padding: 3px 7px;
    font-size: 11px;
    font-weight: bold;
    border-radius: 2px;
    z-index: 1;
  }
  
  .mujinjangTag {
    /*    (:  ) */
    background-color: #ff5252;
  }
  
  .likeButton {
    position: absolute;
    top: 8px;
    right: 8px;
    background: rgba(255, 255, 255, 0.7);
    border: none;
    border-radius: 50%;
    width: 30px;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    z-index: 1;
    padding: 0;
  }
  
  .likeButton:hover {
    background: rgba(255, 255, 255, 0.9);
  }
  
  .heartIcon {
    width: 18px;
    height: 18px;
    color: #ccc; /*    */
    /*  : color: red; */
  }
  
  .info {
    padding: 12px 10px;
  }
  
  .brandName {
    font-size: 13px;
    color: #555;
    margin-bottom: 4px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  
  .productNameLink {
    text-decoration: none;
    color: #222;
  }
  
  .productName {
    font-size: 14px;
    font-weight: normal;
    line-height: 1.4;
    height: 2.8em; /* 2  */
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    margin-bottom: 8px;
  }
  
  .productName:hover {
    text-decoration: underline;
  }
  
  .priceContainer {
    margin-bottom: 6px;
  }
  
  .discountRate {
    color: #ff5252;
    font-size: 16px;
    font-weight: bold;
    margin-right: 6px;
  }
  
  .finalPrice {
    font-size: 16px;
    font-weight: bold;
    color: #000;
  }
  
  .originalPrice {
    font-size: 13px;
    color: #999;
    text-decoration: line-through;
    margin-left: 6px;
  }
  
  .stats {
    font-size: 12px;
    color: #767676;
    display: flex;
    align-items: center;
  }
  
  .statItem {
    margin-right: 8px;
    display: flex;
    align-items: center;
  }
  .statItem:last-child {
    margin-right: 0;
  }
  
