/* Middle.module.css  styles.module.css  */

.element {
  /*  Middle  wrapper  ( ) */
  max-width: 1200px; 
  margin: 0 auto; 
  padding: 20px;
}

.contentWrapper { /*  div-3   */
  /*    */
}

/*  BEST   */
.priceBestHeader {
  display: flex;
  justify-content: space-between; /*   */
  align-items: center; /*    */
  padding: 16px 0; /*   */
  margin-top: 24px; /*    */
  border-bottom: 1px solid #f0f0f0; /*   () */
}

.priceBestTitle {
  font-size: 20px; /*    " BEST"   */
  font-weight: bold;
  color: #000000;
}

.viewAllButton {
  font-size: 14px;
  color: #555; /* " >"   */
  background: none;
  border: none;
  cursor: pointer;
  padding: 0;
}

.viewAllButton:hover {
  text-decoration: underline;
}

/*      */
.priceFilterTabsContainer {
  display: flex;
  /* justify-content: space-around; /*    */
  border-bottom: 1px solid #e0e0e0; /*      */
  margin-top: 8px; /*   */
  position: relative; /*       */
}

.priceFilterTab {
  padding: 12px 16px; /*    (   ) */
  font-size: 15px;    /*    */
  color: #666;         /*     */
  cursor: pointer;
  text-align: center;
  position: relative; /*     */
  flex-grow: 1; /*     () */
  /* min-width: 100px; /*   () */
  white-space: nowrap; /*    */
}

.priceFilterTab:hover {
  color: #000; /*      */
}

.selectedPriceTab {
  color: #000; /*     ( ) */
  font-weight: bold; /*     */
}

/*     (  ) */
.selectedPriceTab::after {
  content: "";
  position: absolute;
  bottom: -1px; /*   border-bottom   */
  left: 0;
  width: 100%;
  height: 2px; /*   */
  background-color: #000000; /*   */
}


/*   ( wrapperRow ) */
.productGrid {
  display: flex;
  flex-wrap: wrap;
  gap: 20px; /*     (ViewWrapperByAnima.module.css  gap ) */
  justify-content: flex-start; /*    */
  padding-top: 24px; /*    */
  position: relative;
  top: -124px;
  left: 45px;
}

/* , ,     */
.loadingMessage,
.errorMessage,
.noProductsMessage {
  padding: 40px 20px; /*    */
  text-align: center;
  width: 100%;
  font-size: 16px;
  color: #555;
}

