/* src/styles/productleft/ThumbnailList.module.css */
.thumbnailList {
  display: flex;
  flex-direction: column;
  gap: 10px;
  /* height: 480px; */
  max-height: 500px;
  overflow-y: auto;
  padding-right: 8px;
  flex-shrink: 0;
  position: relative;
  top: 51px;
}

.thumbnailItem {
  width: 80px;  /*   (   ) */
  height: 100px; /*   (   ,  aspect-ratio ) */
  border: 2px solid #e0e0e0; /*   */
  border-radius: 6px; /*   */
  cursor: pointer;
  overflow: hidden; /*     */
  transition: border-color 0.2s ease-in-out;
  box-sizing: border-box; /* padding, border width, height  */
}

.thumbnailItem:hover {
  border-color: #888; /*      */
}

.thumbnailItem.selected {
  border-color: #007bff; /*     (  ) */
  /* box-shadow: 0 0 5px rgba(0, 123, 255, 0.5); */ /*     () */
}

.thumbnailImage {
  width: 100%;
  height: 100%;
  object-fit: cover; /*     */
  display: block; /*     */
}

/*   (,  ) */
.thumbnailList::-webkit-scrollbar {
  width: 6px;
}

.thumbnailList::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 10px;
}

.thumbnailList::-webkit-scrollbar-thumb {
  background: #c1c1c1;
  border-radius: 10px;
}

.thumbnailList::-webkit-scrollbar-thumb:hover {
  background: #a1a1a1;
}