/* src/components/SearchModal/SearchHeader/SearchHeader.module.css */
.headerContainer {
    display: flex;
    align-items: center;
    padding: 10px 15px;
    background-color: #f8f9fa; /*     */
    border-bottom: 1px solid #e0e0e0;
  }
  
  .searchInput {
    flex-grow: 1;
    border: none;
    background-color: transparent;
    font-size: 15px;
    padding: 8px 0; /*   */
    outline: none;
  }
  
  .searchInput::placeholder {
    color: #888;
  }
  
  .iconButton {
    background: none;
    border: none;
    font-size: 20px; /*   */
    color: #555;
    cursor: pointer;
    padding: 5px;
    margin-left: 10px;
  }
  
  .iconButton:hover {
    color: #000;
  }
  
  .searchIcon {
    font-size: 18px; /*    */
  }
  
  .closeButton {
    font-size: 24px; /* X   */
  }