/* MyOrderHistorySection.module.css */
.orderHistoryContainer {
    /*        /  */
    border: 1px solid #e9e9e9;
    border-radius: 4px;
    background-color: #fff;
    /* : width: 100%; max-width: 700px; margin: 20px auto; */
    position: relative;
    top: 70px;
  }
  
  .header {
    padding: 16px;
    font-size: 16px;
    font-weight: bold;
    color: #333;
    border-bottom: 1px solid #e9e9e9;
  }
  
  .content {
    padding: 0 16px; /* OrderItemCard padding  */
  }
  
  .message {
    padding: 40px 20px;
    text-align: center;
    color: #888;
    font-size: 16px;
  }
  
  .error {
    color: red;
  }