/* src/styles/productleft/ProductImageGallery.module.css */
.galleryContainer {
  display: flex;
  /* +      */
  flex-direction: column;
  gap: 20px; /*       */
  width: 100%;
}

.imageDisplayArea { /*  []      */
  display: flex;
  flex-direction: row; /* (),  ()   */
  gap: 16px;
  width: 100%;
}

/* ThumbnailList MainImageView    CSS      */
/*    ,       */

.productDescriptionArea { /*  []     */
  margin-top: 20px; /*    ( galleryContainer gap ) */
  padding: 15px;
  background-color: #f9f9f9; /*   () */
  border-radius: 6px;
  font-size: 14px;
  line-height: 1.6;
  color: #333;
  /* width: 100%; */ /* galleryContainer column width  100% */
  box-sizing: border-box;
}

.productDescriptionArea p {
  margin: 0; /*     */
  white-space: pre-wrap; /* \n ()   */
}

.noImagePlaceholder { /*      */
  display: flex;
  align-items: center;
  justify-content: center;
  height: 300px; /*    */
  background-color: #f0f0f0;
  color: #aaa;
  font-style: italic;
  border-radius: 8px;
  margin-bottom: 16px; /*   */
}